import { FC, ReactNode, useState } from 'react'
import { motion } from "framer-motion"
import { VscGithubInverted } from 'react-icons/vsc'
import { AiOutlineMail, AiFillLinkedin } from 'react-icons/ai'
import { HiMenu, HiX } from 'react-icons/hi';

const EMAIL = 'iker.camacho.h@gmail.com'
const GIT_LINK = 'https://github.com/icamacho1'
const GIT_TEXT = 'Github'
const LKN_LINK = 'https://www.linkedin.com/in/iker-camacho-hita/'
const LKN_TEXT = 'Linkedin'

export const PAGE_ID = "main-page"

interface CollapseProps {
    link: string
    text: string
    icon: ReactNode
    display?: boolean
}

const CollapseLinkWithIcon: FC<CollapseProps> = ({ link, icon, text, display }) => {
    return (
        <motion.a
            href={link}
            className='flex flex-row gap-3 items-center cursor-pointer group'
            target={"_blank"}
            rel="noreferrer noopener"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
        >
            <div className="text-[#b4befe] group-hover:text-[#cba6f7] transition-colors duration-200">
                {icon}
            </div>
            <span className={`text-[#cdd6f4] group-hover:text-[#f5c2e7] transition-colors duration-200 ${display ? '' : 'hidden md:block'}`}>
                {text}
            </span>
        </motion.a>
    )
}


const Navbar: FC = () => {
    const [isMenuOpen, setIsMenuOpen] = useState(false);

    return (
        <motion.nav
            className='sticky top-0 z-50 bg-[#181825]/80 backdrop-blur-md border-b border-[#45475a]'
            initial={{ y: -100 }}
            animate={{ y: 0 }}
            transition={{ duration: 0.5 }}
        >
            <div className='max-w-7xl mx-auto px-4 sm:px-6 lg:px-8'>
                <div className='flex items-center justify-between h-16'>
                    {/* Logo and Name */}
                    <motion.div
                        className='flex flex-col gap-1'
                        whileHover={{ scale: 1.05 }}
                    >
                        <h1 className='gradient-text font-bold text-2xl font-mono'>Iker Camacho</h1>
                        <p className='text-gray-400 text-sm font-mono'>Full Stack Engineer</p>
                    </motion.div>

                    {/* Desktop Navigation */}
                    <div className='hidden md:flex items-center space-x-8'>
                        <CollapseLinkWithIcon
                            link={GIT_LINK}
                            icon={<VscGithubInverted className='text-xl' />}
                            text={GIT_TEXT}
                            display={true}
                        />
                        <CollapseLinkWithIcon
                            link={`mailto:${EMAIL}`}
                            icon={<AiOutlineMail className='text-xl' />}
                            text={EMAIL}
                        />
                        <CollapseLinkWithIcon
                            link={LKN_LINK}
                            icon={<AiFillLinkedin className='text-xl' />}
                            text={LKN_TEXT}
                        />
                    </div>

                    {/* Mobile menu button */}
                    <div className='md:hidden'>
                        <motion.button
                            onClick={() => setIsMenuOpen(!isMenuOpen)}
                            className='text-[#cdd6f4] hover:text-[#f5c2e7] p-2'
                            whileTap={{ scale: 0.95 }}
                        >
                            {isMenuOpen ? <HiX className='text-2xl' /> : <HiMenu className='text-2xl' />}
                        </motion.button>
                    </div>
                </div>

                {/* Mobile Navigation */}
                {isMenuOpen && (
                    <motion.div
                        className='md:hidden border-t border-[#45475a]'
                        initial={{ opacity: 0, height: 0 }}
                        animate={{ opacity: 1, height: 'auto' }}
                        exit={{ opacity: 0, height: 0 }}
                        transition={{ duration: 0.3 }}
                    >
                                                <div className='px-2 pt-2 pb-3 space-y-3'>
                            <CollapseLinkWithIcon
                                link={GIT_LINK}
                                icon={<VscGithubInverted className='text-xl' />}
                                text={GIT_TEXT}
                                display={true}
                            />
                            <CollapseLinkWithIcon
                                link={`mailto:${EMAIL}`}
                                icon={<AiOutlineMail className='text-xl' />}
                                text={EMAIL}
                                display={true}
                            />
                            <CollapseLinkWithIcon
                                link={LKN_LINK}
                                icon={<AiFillLinkedin className='text-xl' />}
                                text={LKN_TEXT}
                                display={true}
                            />
                        </div>
                    </motion.div>
                )}
            </div>
        </motion.nav>
    )
}

export default Navbar
