import { BrowserRouter, Routes, Route } from "react-router-dom";
import IndexPage from "./pages";


export const PATHS = {
    INDEX: "/",
    PRODUCTS: "/products",
    PRODUCTDETAIL: (id: string = ":id") => `/products/${id}`
}

export const RouterComponent = () => {
    return (
        <BrowserRouter>
            <Routes>
                <Route path={PATHS.INDEX} element={<IndexPage />} />
            </Routes>
        </BrowserRouter>
    )
}
